import React, { useEffect, useState } from 'react';
import {
    Container,
    Navbar, 
    Nav, 
} from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux'
import Logo from '../../../images/logo.png';
import {Link,NavLink,useNavigate,useLocation} from 'react-router-dom';
import './Header.scss';
import { useTranslation } from 'react-i18next'
import {getToken, flushUserSession, logout,getAuthUser, profile_change} from '../../../auth'
import  {fetchUserLogin}  from '../../../redux/actions/UserstateActions';
import { add_badge } from '../../../redux/actions/NotificationstateActions';
import { Profile_modal } from './profile_modal';
import i18n from "../../../i18n";
import axios from 'axios';
import { BellPic } from '../../admin/components/bellPic'
const languages = [
    { value: '', text: "Options" },
    { value: 'en', text: "English" },
    { value: 'nl', text: "Dutch" }
  ]
const Header = (props) => {
   
    const login_status = useSelector(state => state.userState.login_status);
    const AuthUser = getAuthUser();
    const dispatch = useDispatch();
    const {t}=useTranslation();
    //const { i18n, change } = props;
    const [modalShow,setModalShow] = useState();
    //console.log(login_status);
    const navigate = useNavigate();
    const [ active,setActive]=useState();
    const [ click, setClick ] = useState(false);
    const location = useLocation();
    const [lang, setLang] = useState('en');
    const handleChange = e => { 
        setLang(e.target.value)
        i18n.changeLanguage(e.target.value);
    }
    const [ username, setUserName] = useState(AuthUser.name)
    useEffect(()=>{
        const AuthUser = getAuthUser(); 
        setUserName(AuthUser.name)
        const pathname = location.pathname;
        const token = getToken();
        if ( pathname == '/notification')
            setActive(true)
        if (!AuthUser)
           {
                flushUserSession();
           }
        else{
            dispatch(fetchUserLogin(AuthUser))

            
        }
        if (token.length > 0){
            
            axios.get(`${process.env.REACT_APP_API_BASE_URL}/notification/get`,
            {headers: {
                'x-access-token': token,
              }
            })
            .then((res)=>{
                
                if (res.data.length > 0)
                    dispatch(add_badge(true))
                else
                    dispatch(add_badge(false))
            })
        }
    },[])
    const onHide = (data) =>{
        setUserName(data)
        profile_change(data)
        closeModal()
    }
    const closeModal = () => {
        setModalShow(false)
    }
return (
    <div className="header">
        <Navbar bg="white" variant="white" className='py-3'>
            <Container className="navbar-container"> 
                <Profile_modal
                    show={modalShow}
                    onHide={onHide}
                    data={AuthUser}
                    closeModal = {closeModal}
                />              
                <Nav className='align-items-center' style={{gridArea:'tab'}}>
                    <NavLink to="/home"  className="nav-link" >
                        {t('book_ride')}
                    </NavLink>

                    <NavLink to="/history" className="nav-link">
                        {t('book_history')}
                    </NavLink>
                </Nav>
                <Nav className='align-items-center' style={{gridArea:'logo'}}>
                    <NavLink to='/admin/dashboard' className="logo">
                        <img src={Logo} alt="logo" />
                    </NavLink>
                </Nav>
                {login_status?
                <div className='dropdown icon-group'  style={{gridArea:'button'}}>
                    <div className='nav-item'>
                    <select value={lang} onChange={handleChange}>
                            {languages.map(item => {
                                return (<option key={item.value} 
                                value={item.value}>{item.text}</option>);
                            })}
                        </select>
                    </div>
                    <div className='nav-item'>
                        <div className={active?'svg-container active':'svg-container'}>
                            <Link to="/notification">
                               
                                <BellPic/>
                                
                            </Link>
                        </div>
                    </div>
                    <div className='nav-item' onClick={()=>click?setClick(false):setClick(true)}>
                        <div className='user-container' style={{cursor:"pointer"}}>

                            <img src="/images/mark/profile.png" alt='profile'className="profile-icon" width="24px" height="24px"></img>
                            
                        </div>
                        <h6>{username}<span><i className="fas fa-chevron-down"></i></span></h6>
                        <div className={click?'profile active':'profile'}>
                            <h3 onClick={()=>{setModalShow(true); setClick(false);}}>Profile</h3>
                            <h3 onClick={logout}>logout</h3>
                            <div className='triangle'></div>
                        </div>
                    </div>
                </div>:
                <Nav className='align-items-center ' style={{gridArea:'button'}}>
                     <NavLink to="/home"  className="nav-link get-started" >
                        {t('get_started')}
                    </NavLink>
                    <NavLink  to="/login"  className="nav-link login" onClick={()=>dispatch(fetchUserLogin(true))}>
                        {t('login')}
                    </NavLink>
                </Nav>}
            </Container>
        </Navbar> 
    </div>
)};

export { Header };