import React,{useState} from 'react';
import { Link,Navigate } from 'react-router-dom';
import logo from '../../../images/logo.png';
import '../login/login.scss'
import axios from 'axios';
import car_logo from '../../../images/Untitled-2.png'
import {login} from '../../../auth';
require('dotenv').config()
const Register = () => {  
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const validate = (name, value) => {
        switch (name) {
          case "email":
            if (!value) {
              return "Email is Required";
            } else if (
              !value.match(/^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/)
            ) {
              return "Enter a valid email address";
            } else {
              return "";
            }
          case "password":
            if (!value) {
              return "Password is Required";
            } else if (value.length < 6) {
              return "Please fill at least 6 character";
            } else {
              return "";
            }
        //   case "confirmPassword":
        //     if (!value) {
        //       return "Confirm Password Required";
        //     } else if (value !== fields.password) {
        //       return "New Password and Confirm Password Must be Same";
        //     } else {
        //       return "";
        //     }
        //   default: {
        //     return "";
        //   }
        }
      };
    const handleSubmit = () => {
        const validationErrors = {}
        let error =  validate('email',email)
        if (error && error.length > 0)
             validationErrors['email']=error;
        error = validate('password',password)
        if (error && error.length > 0)
             validationErrors['password']=error;
        if (Object.keys(validationErrors).length > 0) {
            setErrors(validationErrors)
        }
        else if (email && password)
            register()
    }
    const [errors, setErrors] = useState({email:'',password:''});
    const handleUserInput = e => {
        
        if (e.target.name == 'email')

                setEmail(e.target.value)
        else
               setPassword(e.target.value)
    }
    const register = () =>{
        //console.log(email)
        //console.log(password)
        axios.post(`${process.env.REACT_APP_API_BASE_URL}/auth/signup`,{email:email,password:password})
        .then((response)=>{
            axios.post(`${process.env.REACT_APP_API_BASE_URL}/auth/signin`,{email:email,password:password})
                .then((res)=>{
                    if (res.status == 200){
                        login(res.data); 
                        window.location = '/home'               
                    }
                    
                })  
        })
        .catch( error => {
            if (error.response.status == '400')
              setErrors({email:error.response.data.message}); 
          })
    }
    return (
        <div className='client-login'>
            <div className='login-left'>
                <div className='img-wrapper'>
                    <img src={logo} alt="logo" width="248px"></img>
                </div>
                <div className='login-form'>
                    <div className='caption'>
                        <h3>Register</h3>
                        <h5>Register yourself to the web to book a Ride</h5>
                    </div>
                    <div className='form-group'>
                            <div className='form'>
                                <label>Email Id:</label>
                                <input required type="email" name="email" value={email} onChange={handleUserInput}></input>
                                
                            </div>
                            <span className="text-danger">{errors.email}</span>
                            <div className='form'>
                                <label>Password:</label>
                                <input required type="password" name="password" value={password} onChange= {handleUserInput}></input>
                               
                            </div>
                            <span className="text-danger">{errors.password}</span>
                            <div className="remember">
                                {/* <Form.Check type="checkbox" className='checkbox' label="Remember me" /> */}
                                <label>
                                    <input type='checkbox'/>
                                    <span></span>
                                    Remember Me
                                </label>
                            </div>
                            <div className='login-button' onClick={handleSubmit}>
                                <h3>Register</h3>
                            </div>
                            <div className='tologin'>
                                <h6 >Already an existing user? <span><Link to="/login">Login here</Link></span></h6>
                            </div>
                    </div>
                    
                </div>
            </div>
            <div className="login-right">
                <img src={car_logo} width="100%"/>
            </div>
        </div>
    )    
};

export { Register };