import React, {useState} from 'react';
import logo from '../../../images/Group.png';
import {NavLink} from 'react-router-dom'
const Sidebar = () => {
    const [ active, setActive ] = useState();
    return (
        <div className='sidebar'>
            
            <div className='menu'>
                    <ul>
                        <li>
                            <NavLink to="/home">
                                <img src={logo} alt="img"></img>
                            </NavLink>
                        </li>
                        <li>
                            <div className='bhhAF'>
                                
                                <NavLink to="/admin/dashboard" 
                                        onMouseEnter={()=>{setActive('dashboard')}}
                                        onMouseLeave={()=>{setActive('')}}
                                        className={active === 'dashboard' ? 'active' :''}
                                        style={({ isActive,isHover }) =>
                                        isActive || isHover
                                            ? {
                                                color: '#FBFDFE',
                                                padding:'18px 18px',
                                                backgroundColor: '#F4730E',
                                                borderRadius:'20px'
                                            }
                                            : { }
                                        }
                                        >
                                    <img src="/images/mark/dashboard.png" alt="dashboard" width="24px" height="24px"/>
                                    <img className='active' src="/images/mark/active_dashboard.png" width="24px" height="24px"/>
                                    <span className='item-content'>dashboard
                                    </span>
                                </NavLink>
                            </div>
                        </li>
                        <li>
                            <div className='bhhAF'>
                                <NavLink to="/admin/vehicle" 
                                        onMouseEnter={()=>{setActive('vehicle')}}
                                        onMouseLeave={()=>{setActive('')}}
                                        className={active === 'vehicle' ? 'active' :''}
                                        style={({ isActive }) =>
                                        isActive
                                            ? {
                                                color: '#FBFDFE',
                                                padding:'18px 18px',
                                                backgroundColor: '#F4730E',
                                                borderRadius:'20px'
                                            }
                                            : { }
                                        }
                                        >
                                    <img src="/images/mark/car.png" alt="dashboard" width="24px" height="24px"/>
                                    <img className='active' src="/images/mark/active_car.png" width="24px" height="24px"/>
                                    <span className='item-content'>Vehicle</span>
                                </NavLink>
                            </div>
                        </li>
                        <li>
                            <div className='bhhAF'>
                                <NavLink to="/admin/booking" 
                                        onMouseEnter={()=>{setActive('booking')}}
                                        onMouseLeave={()=>{setActive('')}}
                                        className={active === 'booking' ? 'active' :''}
                                        style={({ isActive }) =>
                                        isActive
                                            ? {
                                                color: '#FBFDFE',
                                                padding:'18px 18px',
                                                backgroundColor: '#F4730E',
                                                borderRadius:'20px'
                                            }
                                            : { }
                                        }
                                        >
                                    <img src="/images/mark/note-text.png" alt="dashboard" width="24px" height="24px"/>
                                    <img className='active' src="/images/mark/active_note-text.png" width="24px" height="24px"/>
                                    <span className='item-content'>Booking</span>
                                </NavLink>
                            </div>
                        </li>
                        <li>
                            <div className='bhhAF'>
                                <NavLink to="/admin/driver" 
                                            onMouseEnter={()=>{setActive('driver')}}
                                            onMouseLeave={()=>{setActive('')}}
                                            className={active === 'driver' ? 'active' :''}
                                            style={({ isActive }) =>
                                            isActive
                                                ? {
                                                    color: '#FBFDFE',
                                                padding:'18px 18px',
                                                backgroundColor: '#F4730E',
                                                borderRadius:'20px'
                                                }
                                                : { }
                                            }
                                            >            
                                     <img src="/images/mark/people.png" alt="dashboard" width="24px" height="24px"/>
                                     <img className='active' src="/images/mark/active_people.png" width="24px" height="24px"/> 
                                    <span className='item-content'>Drivers</span>
                                </NavLink>
                            </div>
                        </li>
                        <li>
                            <div className='bhhAF'>
                                <NavLink to="/admin/book_ride" 
                                        onMouseEnter={()=>{setActive('rider')}}
                                        onMouseLeave={()=>{setActive('')}}
                                        className={active === 'rider' ? 'active' :''}
                                        style={({ isActive }) =>
                                        isActive
                                            ? {
                                                color: '#FBFDFE',
                                                padding:'18px 18px',
                                                backgroundColor: '#F4730E',
                                                borderRadius:'20px'
                                            }
                                            : { }
                                        }
                                        >
                                     <img src="/images/mark/driving.png" alt="dashboard" width="24px" height="24px"/>
                                     <img className='active' src="/images/mark/active_driving.png" width="24px" height="24px"/>
                                    <span className='item-content'>Book a ride</span>
                                </NavLink>
                            </div>
                        </li>
                    </ul>
            </div>
        </div>)
}
export default Sidebar