import React, { useState , createContext,useMemo, useEffect} from 'react';
import { Link } from 'react-router-dom';
import "react-datepicker/dist/react-datepicker.css";
import Toggle from 'react-toggle';
import "react-toggle/style.css";
import Randomstring from "randomstring";
import GoogleMapReact from 'google-map-react';
import { useNavigate } from 'react-router-dom';
import {
    Nav,
    Container,
    Modal,
    FormControl,
    Row,
    Col
} from 'react-bootstrap';
import { useTranslation } from 'react-i18next'
import { Header } from '../../layout/header';
import AutoComplete from './Autocomplete';
import LocationCheck from '../../../images/location-check.png';
import Trash from '../../../images/trash.png';
import './Home.scss';
import { useDispatch, useSelector } from 'react-redux'
import  {select_booking,save_temp_booking}  from '../../../redux/actions/BookingstateActions';
import axios from 'axios';
import Status_board from '../../layout/Status_board';
export const UserContext = createContext();
const Home = () => {
    const current_time = () => {
        const current_date = new Date()
        const hour = Number(current_date.getHours() < 10) ? '0'+ current_date.getHours():current_date.getHours();
        const minute = Number(current_date.getMinutes()) < 10 ? '0' + current_date.getMinutes() : current_date.getMinutes();
        const current_time= hour+":"+minute;
        return current_time;    
    }
    const temp = useSelector(state => state.bookingState.temp_booking);
    const [selectedOption, setSelectedOption] = useState(temp.pick_location);
    const [startDate, setStartDate] = useState(temp.pick_date);
    const [isSwitchOn, setIsSwitchOn] = useState(temp.isSwitchOn);
    const [stopNum, setStopNum] = useState([{ id: Randomstring.generate(10) }]);
    const [modalShow, setModalShow] = useState(false);
    const [passenger, setPassenger] = useState( temp.passenger? temp.passenger:3);
    const [children, setChildren] = useState(temp.children?temp.children:3);
    const [bag, setBag] = useState(temp.bags? temp.bags:3);
    const [key, setKey] = useState('address');
    const [methodkey, setMethodKey] = useState(1); 
    const [startTime, setStartTime] = useState(temp.pickup_time ? temp.pickup_time : current_time());
    const [dropoff,setDropoff] =useState(temp.dropoff_location);
    const [stop,setStop]=useState([]);
    const [mapInstance, setMapInstance] =  useState();
    const [mapApi,setMapApi] =useState();
    const [dropoffAddress,setDropoffAddress]=useState();
    const [pickupAddress,setPickupAddress]=useState();
   
    useEffect(()=>{
        temp.pickup_date ? setStartDate(temp.pickup_date) : setStartDate(getStartDate(new Date()))
        temp.pickup_time? setStartTime(temp.pickup_time) : setStartTime(current_time())
        setSelectedOption(temp.pick_location)
        setIsSwitchOn(temp.isSwitchOn)
        temp.passenger ? setPassenger(temp.passenger) : setPassenger(0)
        temp.passenger ? setChildren(temp.children) : setChildren(0)
        temp.methodKey ? setMethodKey(temp.methodKey) :setMethodKey(1)
        setDropoff(temp.dropoff_location)
        temp.bags ? setBag(temp.bags) : setBag(0)
        temp.stop ? setStop(temp.stop) : setStop([])
        temp.stopNum ? setStopNum(temp.stopNum):setStopNum([{ id: Randomstring.generate(10) }])
        setMins(temp.mins)
        setHours(temp.hours)
        
    },[])
    const [ errors, setErrors ] = useState({pickup:'',dropoff:'',mins:'',hours:'',startDate:'',startTime:''});
    const handleSUbmit = () => {
        const validationErrors = {}
        let error =  validate(selectedOption);
        if (error && error.length > 0)
            validationErrors['pickup']=error;
        error =  validate(dropoff);
        if (error && error.length > 0)
            validationErrors['dropoff']=error;
        error =  validate(startDate);
        if (error && error.length > 0 && !isSwitchOn)
            validationErrors['startDate']=error;
        error =  validate(startTime);
        if (error && error.length > 0 && !isSwitchOn)
            validationErrors['startTime']=error;
        error =  validate(mins);
        if (error && error.length > 0 && methodkey == 2)
            validationErrors['mins']=error;
        error =  validate(hours);
        if (error && error.length > 0 && methodkey == 2)
            validationErrors['hours']=error;   
        if (Object.keys(validationErrors).length > 0) {
            setErrors(validationErrors)
            //console.log(validationErrors)
        }
        else
            select_vehicle();

    }
    let navigate = useNavigate();
    const validate = (value) => {
            if (!value) 
              return "Field is Required";
            else 
              return "";
      };
    
    const { t } = useTranslation(); 
    const _onClick = (value) => {
        // setLat(value.lat)
        // setLng(value.lng)
    }
    const setCurrentLocation = (()=>{   
        
        if ('geolocation' in navigator) {
            navigator.geolocation.getCurrentPosition((position) => {
                setCenter([position.coords.latitude, position.coords.longitude])
            });
        }
    })
    const apiHasLoaded = (map, maps) => {
        setMapapitLoaded(true)
        setMapApi(maps)
        setMapInstance(map)
        _generateAddress();
    };
    const switch_onChange_handle = () => {
        setIsSwitchOn(!isSwitchOn);
    };
    const updateStop = (rowId, data) => {
        stop.filter(function (value, index, arr){
            //Row to update
            if(value.id == rowId){
             return {address: data}
            }else{
             return {...value}
            }
           })
           setStop([]);
    }
   
    const addStop = () => {
        setStopNum([...stopNum, { id: Randomstring.generate(10) }]);
    }

    const deleteStop = (id) => {
        const filter = stopNum.filter(item => item.id !== id)
        setStopNum(filter);
    }
    const _generatePickupAddress=((id)=> {
        if (mapApi)
        {const geocoder = new mapApi.Geocoder;

            geocoder.geocode({ 'location': { lat: pickuplat, lng: pickuplng } }, (results, status) => {
                
                if (status === 'OK') {
                    if (results[0]) {
                        setZoom(20);
                        setPickupAddress(results[0].formatted_address );
                    } else {
                        window.alert('No results found');
                    }
                } else {
                    window.alert('Geocoder failed due to: ' + status);
                }

            });
        }
    })
    const _generateDropoffAddress=((id)=> {
        
        if (mapApi)
        {const geocoder = new mapApi.Geocoder;
            
            geocoder.geocode({ 'location': { lat: dropofflat, lng: dropofflng } }, (results, status) => {
                
                if (status === 'OK') {
                    if (results[0]) {
                        setZoom(2);
                        setDropoffAddress(results[0].formatted_address );
                    } else {
                        window.alert('No results found');
                    }
                } else {
                    window.alert('Geocoder failed due to: ' + status);
                }

            });
        }
    })
    const _generateAddress=((id)=> {
        
        if (mapApi)
        {const geocoder = new mapApi.Geocoder;
            
            geocoder.geocode({ 'location': { lat: pickuplat, lng: pickuplng } }, (results, status) => {
                
                if (status === 'OK') {
                    if (results[0]) {
                        setZoom(2);
                        setPickupAddress(results[0].formatted_address );
                    } else {
                        window.alert('No results found');
                    }
                } else {
                    window.alert('Geocoder failed due to: ' + status);
                }

            });
        }
    })
    const onMarkerInteraction = (childKey, childProps, mouse) => {
        // setLat(mouse.lat);
        // setLng(mouse.lng);
        setDraggable(false)
    }
    const onMarkerInteractionMouseUp = (childKey, childProps, mouse) => {
        // setState({ draggable: true });

    }

    const _onChange = ({ center, zoom }) => {
        setCenter(center);
        setZoom(zoom);

    }
  
    const [center,setCenter]=useState([]);
    const [draggable,setDraggable]=useState(true);
    const [pickuplat,setPickupLat]=useState();
    const [dropofflat,setDropoffLat] = useState();
    const [pickuplng,setPickupLng]=useState();
    const [dropofflng,setDropoffLng]=useState();
    const [stopplace,setStopPlace] = useState([]);
    const [zoom,setZoom]=useState();
    const [mapApiLoaded,setMapapitLoaded] = useState(false)
    
    const addPickupPlace = (place) => {
        
 
        setPickupLat(place.geometry.location.lat())
        setPickupLng(place.geometry.location.lng())
        _generatePickupAddress()
    };
    const addDropoffPlace = (place) => {
        
        
        setDropoffLat(place.geometry.location.lat())
        setDropoffLng(place.geometry.location.lng())
        _generateDropoffAddress()
    };
    const addStopPlace = (place) => {
        setStopPlace([...stopplace, {lat: place.geometry.location.lat(),lng:place.geometry.location.lng()}])
        _generateAddress()
    }
    const booking = useSelector(state => state.bookingState); 
    const login_status = useSelector(state =>state.userState.login_status)
    const dispatch = useDispatch();
    const select_vehicle = (() => {
        let temp_booking={}
        temp_booking.pickup_date = startDate;
        temp_booking.pickup_time = startTime;
        temp_booking.pick_location = selectedOption;
        temp_booking.passenger =  passenger;
        temp_booking.children = children;
        temp_booking.bags = bag;
        temp_booking.trip_duration_hour = hours;
        temp_booking.trip_duration_min = mins;
        temp_booking.key = key;
        temp_booking.booking_type_id=methodkey;
        temp_booking.dropoff_location = dropoff;
        temp_booking.stop = stop
        temp_booking.isSwitchOn = isSwitchOn;
        temp_booking.stopNum = stopNum
        dispatch(save_temp_booking(temp_booking))
        navigate('/vehicles')
    })
    useEffect (()=>{
        _generateDropoffAddress();
    },[dropofflat,dropofflng])
    useEffect (()=>{
        _generatePickupAddress();
    },[pickuplat,pickuplng])
    const [hours,setHours] = useState(0);
    const [mins, setMins] = useState(20);
    useEffect(()=>{
        setCurrentLocation();
    },[])
    const weekday = ["Sun","Mon","Tues","Wed","Thur","Fri","Sat"];
    const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];
    const getStartDate = (date) => {
        let day = date.getDay();
        day = weekday[day];
        let month = date.getMonth();
        month = monthNames[month]
        const exactdate = date.getDate();
        const year = date.getFullYear();
        const startDate = day + ", "+ month + ", "+exactdate+", "+year;
        return startDate;
    }
    const dateChange = (e) => {
        setStartDateType('text')
        const date = new Date(e.target.value)        
        setStartDate(getStartDate(date))
    }
    const [ startDateType, setStartDateType ] = useState('date')
    const inputTimeChange = (e) => {
        setStartTime(e.target.value)
        
    }
    useEffect(()=>{
        
        if (login_status){
            axios.get(`${process.env.REACT_APP_API_BASE_URL}/booking/get`)
            .then((res)=>{
                dispatch(select_booking(res.data));
            })
            
        }},[login_status]);
    return (
        <div className='home'>
            <Header />

            <div className="main">
                <Container>
                    
                    <Status_board/>
                    <div className='home-row'>
                            <div className='control-board'>
                                <div className='location-control'>
                                    <div className='control-header'>
                                        <div className='pickup-method'>
                                            <h5 className={key == 'address'? "active":''} onClick={()=>{setKey("address")}}><img src={key == 'address'? "/images/mark/location-tick.png":"/images/mark/black-location-tick.png"} alt="location_tick" width="17.25px" height="24px"/>{t('Address')}</h5>
                                            <h5 className={key == 'airport'? "active":''} onClick={()=>{setKey("airport")}}><img src={key == 'airport'? "/images/mark/airplane.png":"/images/mark/black-airplane.png"} alt="airplane" width="20.25px" height="20.6px"/>{t('Airport')}</h5>
                                            <h5 className={key == 'landmark'? "active":''} onClick={()=>{setKey("landmark")}}><img src={key == 'landmark'? "/images/mark/routing-2.png":"/images/mark/black-routing-2.png"} alt="airplane" width="24px" height="24px"/>{t('Landmark')}</h5>
                                        </div>
                                        <div className='control-btns'>
                                            <Nav defaultActiveKey="/#" as="div">
                                                    <h5 className={methodkey === 1 ? 'nav-link active':'nav-link'} onClick={()=>{setMethodKey(1)}}><img src={methodkey === 1 ? "/images/mark/location.png" : "/images/mark/black_location.png"} width="14px" height="14px"></img>{t('Transfer')}</h5>                                              
                                                    <h5 className={methodkey === 2 ? 'nav-link active':'nav-link'} onClick={()=>{setMethodKey(2)}}><img src={methodkey === 2 ? "/images/mark/clock.png" : "/images/mark/black_clock.png"} width="14px" height="14px"></img>{t('Hourly')}</h5>
                                            </Nav>
                                        </div>
                                    </div>
                                    { key === 'address' ? 
                                    <div className='control-body'>
                                        <div className='pickup'>
                                            <label className='title'>{t('Pickup')}:</label>
                                            {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} initialValue={selectedOption} setValue={setSelectedOption} addplace={addPickupPlace} searchkey={key} type={'pickup'}/>:''}
                                            
                                        </div>
                                        <span className="text-danger">{errors.pickup}</span>
                                         {stopNum.map((item, index) => (
                                            <div className='stop d-flex align-items-center justify-content-between' key={item.id}>
                                                <div className='stop-location w-100'>
                                                    <label className='title'>{t('Stop')}{index + 1}:</label>
                                                    {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} value={stop} initialValue={stop[index]} setValue={setStop} addplace={addStopPlace}  searchkey={key}  id={index} type={'stop'}/>:''}
                                                    
                                                </div>
                                                
                                                <div className='trash' onClick={() => deleteStop(item.id)}><img src={Trash} alt="trash" width="24px" height="24px"/></div>
                                            </div>
                                        ))}
                                        <div className='dropoff'>
                                            <label className='title'>{t('dropoff')}:</label>
                                             {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} setValue={setDropoff} initialValue={dropoff} addplace={addDropoffPlace}  searchkey={key}  type={'dropoff'}/>:''}
                                            
                                        </div>
                                        <span className="text-danger">{errors.dropoff}</span>
                                        <div className='field-wrap'>
                                            <div className='ride-now'>{t('RIDE_NOW')}
                                                <Toggle
                                                    defaultChecked={isSwitchOn}
                                                    icons={false}
                                                    onChange={() => switch_onChange_handle()}
                                                />
                                            </div>
                                            <div className='add-stop' onClick={() => addStop()}><i className="fa fa-plus"></i>{t('Add_Stop')}</div>
                                        </div>
                                            {!isSwitchOn?
                                            <div className='date-time'>
                                                <div className='date'>
                                                    <div className='title'>{t('Pickup_date')}:</div>
                                                    <input type="date" id="startDate" placeholder="" onChange={dateChange} />
                                                    <input type="text" id="startDate" defaultValue={startDate}/>
                                                    <span className="text-danger">{errors.startDate}</span>
                                                </div>
                                                
                                                <div className='time'>
                                                    <div className='title'>{t('Pickup_time')}:</div>
                                                    <input type="time" value={startTime} onChange={inputTimeChange} />
                                                    <span className="text-danger">{errors.startTime}</span>
                                                </div>
                                                
                                            </div>:''}
                                            {methodkey === 2 ?
                                                <div className="hour-min-input">
                                                    <div className='hour-input'>
                                                        <h5 onClick={()=>{ parseInt(hours) > 0 ? setHours(hours-1):setHours(0)}}>-</h5>
                                                        <input type="number" value={hours} onChange = {(e)=>setHours(e.target.value)}/>
                                                        <h5 onClick={()=>{setHours(hours+1)}}>+</h5>
                                                        <span className="text-danger">{errors.hours}</span>
                                                    </div>
                                                    
                                                    <div className='hour-input'>
                                                        <h5 onClick={()=>{ parseInt(mins) > 0 ? setMins(mins-1):setMins(0)}}>-</h5>
                                                        <input type="number" value={mins} onChange = {(e)=>setMins(e.target.value)}/>
                                                        <h5 onClick={()=>{setMins(mins+1)}}>+</h5>
                                                        <span className="text-danger">{errors.mins}</span>
                                                    </div>
                                                    
                                                </div>
                                            : ""}
                                    </div>:''}
                                    { key === 'airport' ? 
                                    <div className='control-body'>
                                        <div className='pickup'>
                                            <label className='title'>{t('Pickup')}:</label>
                                            {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} setValue={setSelectedOption} addplace={addPickupPlace} searchkey={key} type={'pickup'}/>:''}
                                            <div className='check' onClick={() => setModalShow(true)} ><img src={LocationCheck} width="24px" height="24px" alt="location-check" /></div>
                                        </div>
                                        <span className="text-danger">{errors.pickup}</span>

                                        <div className='d-flex airport'>
                                                <div className= "input-wrapper">
                                                    <input type="text" placeholder='Select airline'></input>
                                                </div>
                                                <div className= "input-wrapper">
                                                    <input type="number" placeholder='Flight number'></input>
                                                </div>
                                                <div className= "input-wrapper">
                                                    <input type="time" placeholder='arrival time'></input>
                                                </div>
                                            </div>
                                         {stopNum.map((item, index) => (
                                            <div className='stop d-flex align-items-center justify-content-between' key={item.id}>
                                                <div className='stop-location w-100'>
                                                    <label className='title'>{t('Stop')}{index + 1}:</label>
                                                    {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} value={stop} setValue={setStop} addplace={addStopPlace}  searchkey={key}  id={index} type={'stop'}/>:''}
                                                    <div className='check'><img src={LocationCheck} width="24px" height="24px" alt="location-check" /></div>
                                                </div>
                                                
                                                <div className='trash' onClick={() => deleteStop(item.id)}><img src={Trash} width="24px" height="24px" alt="trash" /></div>
                                            </div>
                                        ))}
                                            
                                    
                                        <div className='dropoff'>
                                            <label className='title'>{t('dropoff')}:</label>
                                             {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} setValue={setDropoff} addplace={addDropoffPlace}  searchkey={key}  type={'dropoff'}/>:''}
                                            <div className='check'><img src={LocationCheck} width="24px" height="24px" alt="location-check" /></div>
                                        </div>
                                        <span className="text-danger">{errors.dropoff}</span>
                                        <div className='field-wrap'>
                                            <div className='ride-now'>{t('RIDE_NOW')}
                                                <Toggle
                                                    defaultChecked={isSwitchOn}
                                                    icons={false}
                                                    onChange={() => switch_onChange_handle()}
                                                />
                                            </div>
                                            <div className='add-stop' onClick={() => addStop()}><i className="fa fa-plus"></i>{t('Add_Stop')}</div>
                                        </div>
                                            {!isSwitchOn?
                                            <div className='date-time'>
                                                <div className='date'>
                                                    <div className='title'>{t('Pickup_date')}:</div>
                                                    <input type="date" id="startDate" placeholder="" onChange={dateChange} />
                                                    <input type="text" id="startDate" defaultValue={startDate}/>
                                                    
                                                </div>
                                                <span className="text-danger">{errors.startDate}</span>
                                                <div className='time'>
                                                    <div className='title'>{t('Pickup_time')}:</div>
                                                    <input type="time" value={startTime} onChange={inputTimeChange} />
                                                    <span className="text-danger">{errors.startTime}</span>
                                                </div>
                                                
                                            </div>:''}
                                            {methodkey === 2 ?
                                                <div className="hour-min-input">
                                                    <div className='hour-input'>
                                                        <h5 onClick={()=>{ parseInt(hours) > 0 ? setHours(hours-1):setHours(0)}}>-</h5>
                                                        <input type="number" value={hours} onChange = {(e)=>setHours(e.target.value)}/>
                                                        <h5 onClick={()=>{setHours(hours+1)}}>+</h5>
                                                    </div>
                                                    <span className="text-danger">{errors.hours}</span>
                                                    <div className='hour-input'>
                                                        <h5 onClick={()=>{ parseInt(mins) > 0 ? setMins(mins-1):setMins(0)}}>-</h5>
                                                        <input type="number" value={mins} onChange = {(e)=>setMins(e.target.value)}/>
                                                        <h5 onClick={()=>{setMins(mins+1)}}>+</h5>
                                                    </div>
                                                    <span className="text-danger">{errors.mins}</span>
                                                </div>
                                            : ""}
                                    </div>:''}
                                    { key === 'landmark' ? 
                                    <div className='control-body'>
                                    <div className='pickup'>
                                        <label className='title'>{t('Pickup')}:</label>
                                        {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} setValue={setSelectedOption} addplace={addPickupPlace} searchkey={key} type={'pickup'}/>:''}
                                        <div className='check' onClick={() => setModalShow(true)} ><img src={LocationCheck} width="24px" height="24px" alt="location-check" style={{backgroundColor:'transparent'}}/></div>
                                    </div>
                                    <span className="text-danger">{errors.pickup}</span>
                                     {stopNum.map((item, index) => (
                                        <div className='stop d-flex align-items-center justify-content-between' key={item.id}>
                                            <div className='stop-location w-100'>
                                                <label className='title'>{t('Stop')}{index + 1}:</label>
                                                {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} value={stop} setValue={setStop} addplace={addStopPlace}  searchkey={key}  id={index} type={'stop'}/>:''}
                                                <div className='check'><img src={LocationCheck} width="24px" height="24px" alt="location-check" /></div>
                                            </div>
                                            
                                            <div className='trash' onClick={() => deleteStop(item.id)}><img src={Trash} width="24px" height="24px" alt="trash" /></div>
                                        </div>
                                    ))}
                                    <div className='dropoff'>
                                        <label className='title'>{t('dropoff')}:</label>
                                         {mapApi?<AutoComplete map={mapInstance} mapApi={mapApi} setValue={setDropoff} addplace={addDropoffPlace}  searchkey={key}  type={'dropoff'}/>:''}
                                        <div className='check'><img src={LocationCheck} width="24px" height="24px" alt="location-check" /></div>
                                    </div>
                                    <span className="text-danger">{errors.dropoff}</span>
                                    <div className='field-wrap'>
                                        <div className='ride-now'>{t('RIDE_NOW')}
                                            <Toggle
                                                defaultChecked={isSwitchOn}
                                                icons={false}
                                                onChange={() => switch_onChange_handle()}
                                            />
                                        </div>
                                        <div className='add-stop' onClick={() => addStop()}><i className="fa fa-plus"></i>{t('Add_Stop')}</div>
                                        </div>
                                        {!isSwitchOn?
                                        <div className='date-time'>
                                            <div className='date'>
                                                <div className='title'>{t('Pickup_date')}:</div>
                                                <input type="date" id="startDate" placeholder="" onChange={dateChange} />
                                                <input type="text" id="startDate" defaultValue={startDate}/>
                                                <span className="text-danger">{errors.startDate}</span>
                                            </div>
                                            
                                            <div className='time'>
                                                <div className='title'>{t('Pickup_time')}:</div>
                                                <input type="time" value={startTime} onChange={inputTimeChange} />
                                                <span className="text-danger">{errors.startTime}</span>
                                            </div>
                                            
                                        </div>:''}
                                        {methodkey === 2 ?
                                            <div className="hour-min-input">
                                                <div className='hour-input'>
                                                    <h5 onClick={()=>{ parseInt(hours) > 0 ? setHours(hours-1):setHours(0)}}>-</h5>
                                                    <input type="number" value={hours} onChange = {(e)=>setHours(e.target.value)}/>
                                                    <h5 onClick={()=>{setHours(hours+1)}}>+</h5>
                                                    <span className="text-danger">{errors.hours}</span>
                                                </div>
                                                
                                                <div className='hour-input'>
                                                    <h5 onClick={()=>{ parseInt(mins) > 0 ? setMins(mins-1):setMins(0)}}>-</h5>
                                                    <input type="number" value={mins} onChange = {(e)=>setMins(e.target.value)}/>
                                                    <h5 onClick={()=>{setMins(mins+1)}}>+</h5>
                                                    <span className="text-danger">{errors.mins}</span>
                                                </div>
                                                
                                            </div>
                                        : ""}
                                    </div>
                                   :''}
                                </div>
                                <div className='passenger-control-btn'>
                                    <div className='passenger-control__content'>
                                        <Row>
                                            <Col md={4}>
                                                <div className='passenger'>
                                            <div className='label'>{t('Passengers')}</div>
                                            <div className='body'>
                                                <span className='count'>{passenger}</span>
                                                <div className='btns'>
                                                    <button type='button' className='minus' onClick={() => { passenger>0? setPassenger(passenger - 1):setPassenger(0) }} ><i className='fa-solid fa-minus'></i></button>
                                                    <button type='button' className='plus' onClick={() => { setPassenger(passenger + 1) }} ><i className='fa fa-plus'></i></button>
                                                </div>

                                            </div>
                                                </div>
                                            </Col>
                                            <Col md={4}>
                                                <div className='childrens'>
                                            <div className='label' onClick={select_vehicle}>{t('Children')}</div>
                                            <div className='body'>
                                                <span className='count'>{children}</span>
                                                <div className='btns'>
                                                    <button type='button' className='minus' onClick={() => { children>0 ? setChildren(children - 1):setChildren(0) }}><i className='fa fa-minus'></i></button>
                                                    <button type='button' className='plus' onClick={() => { setChildren(children + 1) }}><i className='fa fa-plus'></i></button>
                                                </div>
                                            </div>
                                                </div>
                                            </Col>
                                            <Col md="4">
                                                <div className='bags'>
                                            <div className='label'>{t('Bags')}</div>
                                            <div className='body'>
                                                <span className='count'>{bag}</span>
                                                <div className='btns'>
                                                    <button type='button' className='minus' onClick={() => { bag> 0 ? setBag(bag - 1):setBag(0) }}><i className='fa fa-minus'></i></button>
                                                    <button type='button' className='plus' onClick={() => { setBag(bag + 1) }}><i className='fa fa-plus'></i></button>
                                                </div>

                                            </div>
                                                </div>
                                            </Col>
                                        </Row>
                                        <h5 className='select-vehicle' onClick ={handleSUbmit} >{t('Select_Vehicle')}</h5>
                                    </div>
                                   
                                </div>
                                
                            </div>
                            <div className='location-map'>
                                <GoogleMapReact
                                    center={center? center:[0,0]}
                                    zoom={zoom?zoom:20}
                                    draggable={true}
                                    onChange={_onChange}
                                    onChildMouseDown={onMarkerInteraction}
                                    onChildMouseUp={onMarkerInteractionMouseUp}
                                    onChildMouseMove={onMarkerInteraction}
                                    onClick={_onClick}
                                    styles=  {
                                        [ {  featureType:'water',  stylers:[{color:'#46bcec'},{visibility:'on'}] },
                                          {  featureType:'landscape',  stylers:[{color:'#f2f2f2'}] },
                                          {  featureType:'road',  stylers:[{saturation:-100},{lightness:45}] },
                                          {  featureType:'road.highway',  stylers:[{visibility:'simplified'}] },
                                          {  featureType:'road.arterial',  elementType:'labels.icon',  stylers:   [{visibility:'off'}] },
                                          {  featureType:'administrative',  elementType:'labels.text.fill',  stylers:   [{color:'#444444'}] },{  featureType:'transit',  stylers:[{visibility:'off'}] },{  featureType:'poi',   stylers:[{visibility:'off'}] }
                                        ]}
                                    bootstrapURLKeys={{
                                        key: 'AIzaSyAygoWDQ-IvoehtL-nJ0qVcHnUkVLsN6Ps',
                                        libraries: ['places', 'geometry'],
                                    }}
                                    yesIWantToUseGoogleMapApiInternals
                                    onGoogleApiLoaded={({ map, maps }) => apiHasLoaded(map, maps)}
                                >
                                </GoogleMapReact>
                            </div>
                    </div>
                </Container>
            </div>
        </div>
    )
};

export { Home };