import { TailSpin } from  'react-loader-spinner'
const LoadingShow = (props) =>{
    return(
        props.show ? 
                    <div className="tailspin" show={props.show} style={{width:'100%', height:'100vh', position: 'fixed', 'z-index': '1101', background: 'rgba(0,0,0,0.2)', top: '0'}}>
                        <TailSpin
                            height="100"
                            width="100"
                            left="50%"
                            top="50%"
                            color='grey'
                            position="absolute"
                            justifyContent="center"
                            alignItems="center"
                            ariaLabel='loading'
                            style={{position:'absolute'}}
                        />
                    </div> : ""
    )
}
export default LoadingShow