import {
    Modal,Row,Col,FormControl
} from 'react-bootstrap';
import React, { useEffect, useState } from 'react';
import axios from 'axios';
import ImageUploading from 'react-images-uploading'; 
import LoadingShow from '../../admin/components/LoadingShow'
export const Profile_modal = (props) => {
    const [name,setName]=useState();
    const [email,setEmail]=useState();
    const [loadingShow, setLoadingShow] = useState();
    const [address,setAddress]=useState();
    const [postcode,setPostcode]=useState();
    const [country,setCountry]=useState();
    const [images, setImages] = useState([]);
    const [errors,setErrors]= useState([])
    const [modalImageurl,setModalImageUrl]=useState([]);
    const maxNumber = 2;
    const onChange = (imageList, addUpdateIndex) => {
        setImages(imageList);
        let filesize = 0;
          for (let i =0;i<imageList.length;i++)
        {
            filesize = filesize + imageList[i].file.size
            
        }
        
        if(filesize >  500 * 1024 * 5)
            setErrors({file:'image size is too big'})
        else
            setErrors({file:''})
    };
    const confirm = (async ()=>{
        const formData = new FormData();
        let temp_files = []
        
        if (images)
            images.map((image)=>{
                temp_files.push(image.file)
            })
        
        {temp_files.map(file=>{
            formData.append("uploadImages", file);
          });}
          let filesize = 0;
          for (let i =0;i<images.length;i++)
        {
            filesize = filesize + images[i].file.size
            
        }
        
        if (errors && errors.file && errors.file.length > 0)
        {
          //console.log('error')
        }
        else {
           setLoadingShow(true)
          const result = await axios.post(`${process.env.REACT_APP_API_BASE_URL}/driver/upload`,formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
              'boundary':'${form_data._boundary}'
            },
          })
          
          if (temp_files.length > 0)
            upload_user_data(result.data.data[0].id);
          else
            upload_user_data()
        }
    })
    const upload_user_data = ((id = null) => {
        let data = {}
        data.first_name = name
        data.email = email
        data.address = address
        data.postcode = postcode
        data.country = country
        if (id)
           data.img_url = id
        axios.post(`${process.env.REACT_APP_API_BASE_URL}/auth/update`,data)
            .then((res)=>{
                const data = res.data
                setLoadingShow(false)
                props.onHide(data.username);
            })
    })
    useEffect(()=>{
        
        if (props.data)
        axios.post(`${process.env.REACT_APP_API_BASE_URL}/auth/info`,{email:props.data.email})
            .then((res)=>{
                const data = res.data;
                setName(data.username)
                setAddress(data.address)
                setCountry(data.country)
                setPostcode(data.postcode)
                setEmail(data.email)
                let temp=[];
                temp.push({name:data.imgurl})
                if (data.imgurl)
                    setModalImageUrl(temp)
                else
                    setModalImageUrl([{name:'default.png'}])
            })
    },[])
    return (
        <Modal
            show = {props.show}
            onHide = {props.closeModal}
            className="profile-modal"
            aria-labelledby="contained-modal-title-vcenter"
            centered
        >
            <Modal.Body>
                <Row>
                    <Col md={6}>
                        <div className='header-wrap'>
                            <div className='title'>
                                Your Profile
                            </div>
                            <div className='description'>
                                <h6>Brief us with your personal information so the we can better serve you</h6>
                            </div>

                                <ImageUploading
                                            value={images}
                                            onChange={onChange}
                                            maxNumber={maxNumber}
                                            dataURLKey="data_url"
                                        >
                                            {({
                                            multiple,
                                            imageList,
                                            onImageUpload,
                                            onImageRemoveAll,
                                            onImageUpdate,
                                            onImageRemove,
                                            isDragging,
                                            dragProps
                                            }) => (
                                                <>
                                                <div className="modal-left__thumb" > 
                                                   <div className="img-wrapper">
                                                        {imageList.length > 0 ?
                                                            <>
                                                            {imageList.map((image, index) => (
                                                                <img className="user-image" src={image.data_url} alt="car" key={index} width="195px" height="112px" />
                                                           ))}
                                                           </>:
                                                           <div style={{position:'relative', display:'inline-table'}}>
                                                                <img style={{width:'140px',height:'140px',borderRadius:'50%'}} src={`${process.env.REACT_APP_IMAGE_BASE_URL+modalImageurl[0].name}`} alt="car"/>
                                                                {/* <img onClick={onImageUpload} src = "/images/mark/camera.png" width="18px" height="18px" /> */}
                                                            </div>}
                                                            <div className='i-wrapper'>
                                                                <img onClick={onImageUpload} src = "/images/mark/camera.png" width="18px" height="18px" />
                                                            </div>
                                                        
                                                    </div>                                                  
                                                </div>
                                            </>
                                            )}
                                </ImageUploading>
                                <span className="text-danger">{errors.file}</span>
                        </div>
                    </Col>
                    <Col md={6}>
                        <div className='content-wrap '>
                            <div className='street d-flex input-wrapper align-items-center'>
                                <div className='label'>Name:</div>
                                <input
                                    aria-label="Username"
                                    aria-describedby="basic-addon1"
                                    value={name}
                                    onChange={(e)=>setName(e.target.value)}
                                />
                            </div>
                            <div className='city d-flex input-wrapper align-items-center'>
                                <div className='label'>Email:</div>
                                <input
                                    aria-label="Username"
                                    aria-describedby="basic-addon1"
                                    value={email}
                                    onChange={(e)=>setEmail(e.target.value)}
                                />
                            </div>
                            <div className='state d-flex input-wrapper align-items-center'>
                                <div className='label'>Address:</div>
                                <input
                                    aria-label="Username"
                                    aria-describedby="basic-addon1"
                                    value={address}
                                    onChange={(e)=>setAddress(e.target.value)}
                                />
                            </div>
                            <div className='postalcode d-flex input-wrapper align-items-center'>
                                <div className='label'>Postal Code:</div>
                                <input
                                    aria-label="Username"
                                    aria-describedby="basic-addon1"
                                    value={postcode}
                                    onChange={(e)=>setPostcode(e.target.value)}
                                />
                            </div>
                            <div className='country d-flex input-wrapper align-items-center'>
                                <div className='label'>Country:</div>
                                <input
                                    aria-label="Username"
                                    aria-describedby="basic-addon1"
                                    value={country}
                                    onChange={(e)=>setCountry(e.target.value)}
                                />
                            </div>
                            <div className='btns'>
                                <h6 className='update' onClick={()=>confirm()}>Continue</h6>
                                <button type='button' className='edit' onClick={() => props.closeModal()}>Skip</button>
                            </div>
                        </div>
                        
                    </Col>
                </Row>
            </Modal.Body>
            <LoadingShow show={loadingShow}></LoadingShow>   
        </Modal>
    );
}