import React, {useState} from 'react';
const DynamicTimer = () => {
    let time = new Date().toLocaleTimeString();
    let [currentTime, changeTime] = React.useState(time);
    const monthNames = ["January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
    ];
    function checkTime() {
        let temp_time = new Date();
        let temp_day = Number(temp_time.getDate());
        switch (temp_day){
            case  1 || 21 || 31:
                temp_day = temp_day + 'st'
                break
            case  2 || 22:
                temp_day = temp_day + 'nd'
                break
            case  3 || 23:
                temp_day = temp_day + 'rd'
                break
            default:
                temp_day = temp_day + 'th'
                break

        }
        changeTime({
            hour:temp_time.getHours() % 12,
            minute:temp_time.getMinutes(),
            pm:temp_time.getHours() > 11 ? 'PM':'AM',
            day:temp_day,
            month : monthNames[temp_time.getMonth()],
            year: temp_time.getFullYear()

        })
    }
    setInterval(checkTime, 1000);
    return(
        <>
        {currentTime?
            <h5 className='date'>{currentTime.hour}:{currentTime.minute} {currentTime.pm} <span className="at">at</span> {currentTime.day} {currentTime.month} {currentTime.year}</h5>:''}
        </>
    )
}
export {DynamicTimer}